




















































import {
  defineComponent, reactive, computed, ref, toRefs, watch, markRaw, onMounted,
} from '@vue/composition-api';

export default defineComponent({
  name: 'Meet-review__header',
  components: {},
  props: {
    col: {
      type: Object,
      default: () => ({}),
    },
    allCols: {
      type: Array as ()=> any[],
      default: () => [],
    },
    isSearch: {
      type: Boolean,
      default: false,
    },
    searchValue: {
      type: String,
      default: '',
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({
      temCurrentAllCols: [] as any[],
      currentAllCols: [] as any[],
      currentCol: {} as any,
      currentSearchValue: '',
      displayTypes: [{
        title: '大图',
        colsNum: 4,
      }, {
        title: '中图',
        colsNum: 8,
      }, {
        title: '小图',
        colsNum: 16,
      }],
      colsNum: 8 as 4 | 8 | 16,
    });
    const title = computed(() => {
      if (props.isSearch) return '搜索结果';
      if (state.currentCol.listTitle) return state.currentCol.listTitle;

      return '全部';
    });
    watch(() => props.allCols, () => {
      state.currentAllCols = props.allCols.map(item => ({ ...item, isShow: true }));
      const showCols = state.currentAllCols.filter(item => item.isShow);
      emit('cols-change', showCols);
    }, { immediate: true });
    watch(() => props.col, () => {
      state.currentCol = props.col;
    });
    watch(() => props.searchValue, () => {
      state.currentSearchValue = props.searchValue;
    }, { immediate: true });
    const setting = reactive({
      show: false,
      open() {
        state.temCurrentAllCols = JSON.parse(JSON.stringify(state.currentAllCols));
      },
      done() {
        const showCols = state.currentAllCols.filter(item => item.isShow);
        setting.show = false;
        emit('cols-change', showCols);
      },
      cancel() {
        state.currentAllCols = JSON.parse(JSON.stringify(state.temCurrentAllCols));
        setting.show = false;
      },
    });


    const changeCols = (colsNum:4|8|16) => {
      state.colsNum = colsNum;
      emit('cols-num-change', colsNum);
    };
    const search = () => {
      emit('search', state.currentSearchValue);
    };

    const serachBack = () => {
      emit('search-back');
    };


    return {
      ...toRefs(state),
      setting,
      changeCols,
      search,
      title,
      serachBack,
    };
  },
});
